import React from 'react';
import { connect } from 'react-redux'
import DrinksCount from '../../../drinks/partials/DrinksCount_'
import './category-aside.scss'
import './category-aside-list.scss'

const stateToProps = state => {
    return {
        drinks: state.drinks.drinks
    };
};

function dispatchToProps(dispatch) {
    return {
        setDrinksPrefsListView: () => 'action'
    };
}

function CategoryAsideList({ drinks }) {
    return (<div className='cat-aside-list'>
        <input className='cat-aside-list'
            placeholder='nazwa drinka lub składnika'></input>

        <button className='cat-aside-list'>Wyczyść</button>

        <DrinksCount drinks={drinks} />
    </div>)
}

export default connect(stateToProps, dispatchToProps)(CategoryAsideList)